import React from "react";
import { Layout } from "../components/Layout";

const About = () => {
  return (
    <Layout>
      <div className="min-h-screen bg-gray-900 text-white p-6 flex flex-col items-center">
        <h1 className="text-4xl font-bold mb-8 text-center">About Us</h1>
        <div className="max-w-4xl mx-auto">
          <p className="text-lg mb-6">
            Welcome to the NIT Rourkela Clubs Portal! This platform is dedicated
            to bridging the gap between students and the numerous clubs and
            opportunities available at NIT Rourkela.
          </p>
          <p className="text-lg mb-6">
            Many students miss out on vital information about clubs and the
            opportunities they offer. Our aim is to ensure that every student
            can access detailed information about each club, the resources
            needed to prepare for inductions, and even apply to join the clubs
            directly through this website.
          </p>
          <div className="bg-gray-800 p-6 rounded-lg shadow-lg mb-6">
            <h2 className="text-3xl font-semibold mb-4 text-green-400">
              Features
            </h2>
            <ul className="list-disc list-inside space-y-2">
              <li>
                <span className="font-semibold text-green-300">
                  Club Details:
                </span>{" "}
                Explore comprehensive information about various clubs at NIT
                Rourkela, including their missions,agenda, events and
                expectations.
              </li>
              <li>
                <span className="font-semibold text-green-300">Resources:</span>{" "}
                Find out what resources you need to prepare for club inductions
                and how to maximize your chances of getting selected.
              </li>
              <li>
                <span className="font-semibold text-green-300">Apply:</span>{" "}
                Submit your applications to join clubs directly through the
                portal.
              </li>
              <li>
                <span className="font-semibold text-green-300">
                  Categories:
                </span>{" "}
                Browse clubs by categories such as Tech, Cultural, Literary,
                Sports, Entrepreneurship, and Social Activity.
              </li>
              <li>
                <span className="font-semibold text-green-300">
                  Admin/Executive Access:
                </span>{" "}
                Club executives and admins can easily view, accept, or reject
                applications with personalized messages.
              </li>
            </ul>
          </div>
          <p className="text-lg">
            Join us in creating a vibrant and connected student community at NIT
            Rourkela. Discover new interests, develop your skills, and become an
            active part of the campus life through our portal.
          </p>
        </div>
      </div>
    </Layout>
  );
};

export default About;
